<template>
  <div id="PCHeader" :class="{fixed:isfixed}">
    <div class="nav_warp">
      <nav>
        <li v-for="item in nav" @click="gotoUrl(item.url)">{{item.name}}</li>
        <li class="user" v-if="isLogin">
          <em @click="gotoMyinfo()" v-if="userInfo">{{userInfo.trueName}}</em>|
          <em @click="outSign()">退出</em></li>
        <li class="user" v-if="!isLogin">
            <em @click="gotoLogin()">登录</em>|
            <em @click="gotoLogin()">注册</em></li>
          </li>
        <li class="publish" @click="gotoUrl('http://www.07358.com/post/index')">免费发布房源</li>
      </nav>
    </div>

    <div class="logo_search">
      <div class="logo">
        <img src="../../images/main/logo.jpg">
        <h1>问答</h1>
      </div>
      <div class="search">
        <div class="left" @click="searchTypeAction()">
          <span>{{searchKey.name}}</span>
          <img src="../../images/main/triangle_icon.png">
        </div>
        <input placeholder="房产知识有疑问？来搜搜吧~" v-model="searchStr"/>
        <div class="right" @click="gotoSearch()"><img src="../../images/main/search_icon.png"></div>
        <div class="search_pop" v-if="showSearchPop">
          <span @click="setSearchType({ask_type_id:0,name:'全部'})">全部</span>
          <span v-for="item in askLB" @click="setSearchType(item)">{{item.name}}</span>
        </div>
      </div>


    </div>


    <!-- <i>免费发布房源</i> -->
  </div>
</template>

<script>
import { Toast,Indicator} from 'mint-ui'
import {getStore, setStore,removeStore} from '../../components/common/mUtils'
import { askLb, userData} from '../../api/getData'
import {mapState, mapActions} from 'vuex'
export default {
  data(){
    return{
      userID:'',
      userToken:'',
      userInfo:null,
      msgList:[],
      isLogin:false,
      fixed:false,
      searchStr:'',
      nav:[
        {id:1,name:'首页',url:'http://www.07358.com'},
        {id:2,name:'新房',url:'http://www.07358.com/lp'},
        {id:3,name:'二手房',url:'http://www.07358.com/esf'},
        {id:4,name:'租房',url:'http://www.07358.com/rent'},
        {id:5,name:'门面',url:'http://www.07358.com/business'},
        {id:6,name:'查房价',url:'http://www.07358.com/lp/price'},
        {id:7,name:'产房资讯',url:'http://www.07358.com/news/all'},
        {id:8,name:'商务服务',url:'http://www.07358.com/zhuanti/2017/cfjr/'},
        {id:9,name:'房产咨询',url:'/'},
        // {id:10,name:'生活缴费',url:''},
        {id:11,name:'移动APP',url:'http://app.07358.com/'},
      ],
      typeArr:[],
      searchKey:{
        id:0,name:'全部'
      },
      showSearchPop:false,
    }
  },
  props: ['title','isfixed',],
  created(){
    this.userToken = getStore('userToken')
    this.userID = getStore("userID")
    if (this.userID) {
      this.isLogin = true
      this.getUser()
    }
  },
  mounted(){
    //this.getAskLb()
    this.getAskLBData()
  },
  components:{
  },
  computed:{
    ...mapState(['askLB']),
  },
  methods: {
    ...mapActions(['getAskLBData'],['gotoMyinfo']),
    // gotoMyinfo(){
    //   this.$router.push('/myAsk_pc')
    // },
    gotoLogin(){
      this.$router.push('/login_pc')
    },
    gotoUrl(url){
      window.location.href=url;
    },
    searchTypeAction(){
      this.showSearchPop = !this.showSearchPop;
    },
    setSearchType(item){
      this.showSearchPop = false;
      this.searchKey = item;
    },
    gotoSearch(){
      if (this.searchStr != '') {
        console.log(this.searchStr)
        let str = encodeURI(encodeURI(this.searchStr));
        this.$router.push({path: '/search_pc', query:{keyStr:str,type:1,sid:this.searchKey.ask_type_id}})
      }
    },
    outSign(){
      removeStore('userID')
      removeStore('userToken')
      this.isLogin = 0
      Toast('退出成功')
      this.$router.push('/')
    },
    // 获取用户信息
    async getUser(){
      try{
        this.userInfo = await userData({token:this.userToken})
      }catch(err){
        console.log(err);
      }
    },
    // 获取问题类别
    async getAskLb(){
      try{
        let result = await askLb()
        this.typeArr = result;
      } catch(err){
        console.log(err);
      }
    },
  },

}
</script>

<style scoped>
#PCHeader{
  width: 100%;
  /*background-color: #26a2f;*/
  background-color: #fff;
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  box-sizing: border-box;
  position: relative;
  text-align: center;
  white-space: nowrap;
}

#PCHeader .nav_warp{
    background: #009cff none repeat scroll 0 0;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
}

#PCHeader nav{
  width: 1200px;
  height: 50px;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  /*border-left: 1px solid #fff;
  border-right: 1px solid #fff;*/
}

#PCHeader nav li{
  margin: 0px 15px;
  font-weight: bold;
  cursor: pointer;
}

#PCHeader nav .publish{
  width: 110px;
}

#PCHeader nav .user{
  margin-left: 30px;
  margin-right: 70px;
}

#PCHeader nav .user em{
  font-style: normal;
  padding: 0px 10px;
  font-weight: bold;
}

#CFWHeader span{
  flex-grow: 1;
}

#CFWHeader .left{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 55px;;
  color: #fff;
  -webkit-appearance: none;
  border: 0;
  box-shadow: none;
}

#CFWHeader .left .back_icon{
  width: 8px;
  height: 18px;
  margin-left: 10px;
  margin-right: 5px;
}

#CFWHeader .left span{
  font-size: 15px;
  line-height: 15px
}

#CFWHeader .right{
  width: 55px;
  font-size: 14px;
  line-height: 14px;
  background-color: transparent;
  border: 0;
  box-shadow: none;
  display: inline-block;
  padding: 0;
}

.fixed{
  position: fixed;
  top: 0px;
  left: 0px;
  z-index: 999;
}

#PCHeader .logo_search{
  background: #fff;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  /*padding: 10px;*/
  width: 1200px;
  font-size: 15px;
}

#PCHeader .logo_search .logo{
  margin: 20px 20px;
  display: flex;
  flex-direction: row;
  align-items: center;
}
#PCHeader .logo_search .logo img{
  margin-right: 30px;
}

#PCHeader .logo_search .logo h1{
  color: #009cff;
  font-size: 30px;
  padding: 0px 30px;
  border-left: 1px solid #ddd;
  line-height: 50px;
}

#PCHeader .logo_search .search{
  background: rgb(108,180,250);
  height: 46px;
  width: 500px;
  margin-right: 20px;
  display: flex;
  flex-direction: row;
  align-items: center;
  position: relative;
}

#PCHeader .logo_search .search .left{
  width: 100px;
  height: 40px;
  background-color: #fff;
  margin-left: 3px;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
}

#PCHeader .logo_search .search .left span{
  color: #999;
}

#PCHeader .logo_search .search .left img{
  width: 15px;
  height: 15px;
  margin-left: 5px;
  margin-right: 10px;
}

#PCHeader .logo_search .search input{
  flex-grow: 1;
  height: 40px;
}

#PCHeader .logo_search .search .right{
  width: 80px;
  height: 40px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

#PCHeader .logo_search .search .right img{
  width: 30px;
  height: 30px;
}

#PCHeader .logo_search .search .search_pop{
  position: absolute;
  bottom: -180px;
  left: 0px;
  width: 90px;
  border: 2px solid rgb(108,180,250);
  display: flex;
  flex-direction: column;
  background-color: #fff;
}

#PCHeader .logo_search .search .search_pop span{
  color: #000;
  height: 35px;
  line-height: 35px;
}

#PCHeader .logo_search .search .search_pop span:hover{
  background-color: #eeeeee;
  font-weight: bold;
}




</style>



