<template>
  <div class="common_nav">
    <nav @click="gotoAddress('index')">
      <div><span :class="tid==1 ? 'selectd' :'' ">问答首页</span></div></nav>

    <nav @click="gotoAddress('category_pc')">
      <div><span :class="tid==2 ? 'selectd' :'' ">问答分类</span></div></nav>

    <nav @click="gotoAddress('baike_pc')">
      <div><span :class="tid==3 ? 'selectd' :'' ">问答百科</span></div></nav>

    <nav @click="gotoAddress('expertlist_pc')">
      <div><span :class="tid==6 ? 'selectd' :'' ">问答专家</span></div></nav>

    <nav @click="gotoAddress('publishAsk_pc',true)">
      <div><span :class="tid==4 ? 'selectd' :'' ">在线提问</span></div></nav>

    <nav @click="gotoAddress('myAsk_pc',true)">
      <div><span :class="tid==5 ? 'selectd' :'' ">我的问答</span></div></nav>
  </div>
</template>

<script>
import { Toast,Indicator} from 'mint-ui'
import {getStore, setStore,removeStore} from '../../components/common/mUtils'
export default {
  data(){
    return{
      userID:'',
      userToken:'',
      userInfo:null,
      isLogin:false,
      fixed:false,
      searchStr:'',
      typeArr:[],
    }
  },
  props: ['title','isfixed','tid'],
  created(){
    this.userToken = getStore('userToken')
    this.userID = getStore("userID")
    if (this.userID) {
      this.isLogin = true
      //this.getUserData()
    }
  },
  mounted(){
    //this.getAskType()
  },
  components:{
  },
  methods: {
    gotoAddress(path,login){
      if (login) {
        if (this.isLogin) {
          this.$router.push(path)
        }else {
          this.$router.push('/login_pc')
        }
      }else {
          this.$router.push(path)
      }
    },
    handleBack(){
      this.$router.go(-1);
    },
    // 跳转到板块
    gotoBk(){
      this.$router.push('/arealist')
    },
    gotoMyinfo(){
      this.$router.push('/myAsk_pc')
    },
    gotoLogin(){
      this.$router.push('/login_pc')
    },
    gotoUrl(url){
      window.location.href=url;
    },
    searchTypeAction(){
      this.showSearchPop = !this.showSearchPop;
    },
    setSearchType(item){
      this.showSearchPop = false;
      this.searchKey = item;
    },
    gotoSearch(){
      if (this.searchStr != '') {
        let str = encodeURI(encodeURI(this.searchStr));
        this.$router.push({path: '/search', query:{keyStr:str,type:1,sid:this.$route.query.tid}})
      }else {
        this.$router.push({path: '/search', query:{type:1,sid:this.$route.query.tid}})
      }
    },
  },

}
</script>

<style scoped>

/*****   导航条   ********/
.common_nav{
  height: 50px;
  display: flex; flex-direction: row; justify-content: flex-start;
  align-items: center; background-color: #fff;
}

.common_nav nav{
  display: inline-block;
  width: 110px;height: 20px;
  border-right: 1px solid #ddd;
}

.common_nav nav div{
  height: 100%; display: flex;
  flex-direction: column; justify-content: center; align-items: center;
}

.common_nav nav div img{ width: 30%; margin-bottom: 5px; }

.common_nav nav div span{ font-size: 14px; }

.common_nav nav div .selectd{ color: #0089e4; font-weight: bold; }

.common_nav nav div span:hover{ color: #0089e4; font-weight: bold; cursor: pointer;}

/*****   导航条 End  ********/



</style>



