<template>
  <div id="baike_pc">
    <PCHeader></PCHeader>

    <section class="nav">
      <PCCommonNav :tid="4"></PCCommonNav>
    </section>

    <section class="main_center">
      <div class="left">
        <section class="wrap">
          <div class="PCheader_title">
            <div class="expert" v-if="expert">
              <span>向专家</span>
              <img :src="getUserAvatar">
              <h3>{{expert.trueName}}</h3>
              <span>提问</span>
            </div>
            <h2 style="margin-left:10px;" v-if="!expert">发布问题</h2>
          </div>
          <h4 class="title_dec">一句话描述您的疑问<i>(必填)</i></h4>
          <input v-model="askParams.title" />
          <h4 class="title_dec">问题描述<i>(选填)</i></h4>
          <div class="ask">
            <textarea placeholder="描述尽量详细，以便于专家能够回答更精准~" v-model="askParams.nr">
            </textarea>
            <div class="upImg">
              <div class="imgWrap">
                <div class="img" v-for="item,i in askParams.img" v-if="askParams.img.length">
                  <img :src="item" @click="showQImg(item)">
                  <i @click="delUpImg(i)"><!--<img src="../../images/main/img_del.png">-->删除</i>
                </div>
                <div class="img" v-if="askParams.img.length == 0" style="width:30px;">
                  <upload flag="'thumbnail'" @complete="uploadComplete" @progress="uploadProgress">
                        <img src="../../images/main/img_icon.png">
                  </upload>
                </div>
                <div class="img" v-if="askParams.img.length < 3 && askParams.img.length > 0">
                  <upload flag="'thumbnail'" @complete="uploadComplete" @progress="uploadProgress">
                        <img src="../../images/main/add_img.png">
                  </upload>
                </div>

              </div>
              <p v-if="askParams.img.length == 0">一共可以选择3张图片</p>
              <p v-if="askParams.img.length">已选{{askParams.img.length}}张，还可选择{{3 - askParams.img.length}}张</p>
            </div>
          </div>
          <h4 class="title_dec">问题分类<i>(必填)</i></h4>
          <div class="ask_category">
            <span v-for="item in lbArr" @click="setType(item)">
              <img v-if="askParams.lb == item.ask_type_id" src="../../images/main/radio_icon_s.png">
              <img v-if="askParams.lb != item.ask_type_id" src="../../images/main/radio_icon.png">
              {{item.name}}
            </span>
          </div>
          <div class="btn"><i @click="postAsk()">提交</i></div>
        </section>

        <!-- <section class="baike_list" style="margin-top:0px;" v-for="item,index in typeList">
          <PCBaikeItem :itemData="item" :index="index"></PCBaikeItem>
        </section> -->

      </div>
      <div class="right">
        <section class="askDec">
          <div class="PCheader_title"><h2 style="margin-left:10px;">如何更有效的提出问题</h2></div>
          <div class="item">
              <span  v-for="item,index in askDec"><i>{{index+1}}</i>{{item.name}}</span>
          </div>
        </section>

      </div>
    </section>

    <PCFooter></PCFooter>

  </div>
</template>

<script>
import { Toast,Indicator} from 'mint-ui'
import {getStore,imgBaseUrl,upImgBaseUrl,getUserAvatar} from '../../components/common/mUtils'
import PCHeader from '../../components/header/PCHeader'
import PCFooter from '../../components/footer/PCFooter'
import upload from '../../components/common/upload'
import PCCommonNav from '../../components/common/PCCommonNav'
export default {
  data(){
    return{
      imgBaseUrl,
      upImgBaseUrl,
      userID:'',
      isLogin:false,
      lbArr:[],
      list:[],
      ask_category:[],
      sid:1,
      typeList:[],
      expert:null,
      zjid:0,
      askParams:{
        title:'',
        nr:'',
        lb:1,
        imgs:'',
        img:[],
        fen:0,
        zj:0,
        y_time:0
      },
      askDec:[
        {name:'描述现象'},
        {name:'不要加工'},
        {name:'言简意赅'},
        {name:'说明目的'},
      ]
    }
  },
  created(){
    this.userToken = getStore('userToken')
    this.userID = getStore("userID")
    this.zjid = this.$route.query.zjid;
    if (this.userID) {
      this.isLogin = true
    }

    // 检测是不是移动端
    this.isMobile()
  },
  filters:{
    getNumber(idx){
      ++idx
      if (idx < 10) {
        idx = '0'+idx
      }
      return idx;
    }
  },
  mounted(){
    // 获取问题类别
    this.getAskType()
    if (this.isLogin && this.zjid) {
      this.askParams.zj = this.zjid
      this.getExpert()
    }

  },
  components:{
    PCHeader,
    PCFooter,
    upload,
    PCCommonNav
  },
  computed:{
    getUserAvatar:function (){
      return getUserAvatar(this.expert.avatarPath)
    }
  },
  methods: {
    gotoAddress(path,login){
      if (login) {
        if (this.isLogin) {
          this.$router.push(path)
        }else {
          this.$router.push('/login_pc')
        }
      }else {
          this.$router.push(path)
      }
    },
    setType(item){
      this.askParams.lb = item.ask_type_id
    },
    // 获取问题类别
    getAskType(){
      Indicator.open();
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/lb'
      // console.log(getUrl)
      this.$http.get(getUrl).then(response => {
        let code = response.data.code
        if (code == 200) {
          this.lbArr = response.data.result
        }
        Indicator.close();
      }, response => {
        Toast('请求失败');
        console.log(response);
        Indicator.close();
      });
    },
    postAsk(){
      if (!this.validateFormData()) {
        return
      }
      Indicator.open('提交中...');
      let imgsUrls = ''

      this.askParams.img.forEach((item) =>{
        imgsUrls = imgsUrls == '' ? item : imgsUrls+','+item
      })
      let params = {
        'title' : this.askParams.title,
        'nr':this.askParams.nr,
        'lb':this.askParams.lb,
        'zj':this.askParams.zj,
        'imgs':imgsUrls,
      }

      // 发起请求
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/tiwen?token='+this.userToken
      this.axios.post(getUrl,this.objToformData(params))
      .then(function (response) {
        let code = response.data.code
        if (code == 200) {
          Toast(response.data.message);
          this.askParams ={
            title:'',
            nr:'',
            lb:1,
            imgs:'',
            img:[],
            fen:0,
            y_time:0
          }
        }
        setTimeout(() => {
          this.gotoAddress('myAsk_pc',true)
        }, 1000);
        Indicator.close();
      }.bind(this))
      .catch(function (error) {
        console.log(error);
      });
    },
    // 表单数据验证
    validateFormData(){
      if (this.askParams.title == '') {
        Toast('请输入问题标题！');
        return
      }

      // if (this.askParams.nr == '') {
      //   Toast('请输入描述！');
      //   return
      // }

      if (this.askParams.lb == 0) {
        Toast('请选择问题类别！');
        return
      }
      return true
    },
    // 参数转化成表单对象
    objToformData(obj){
      // let params = new URLSearchParams()
      let params = new FormData()
      Object.keys(obj).forEach( (key, i) => {
        params.append(key, obj[key]);
      });
      return params;
    },
    // 上传图片过程中的回调
    uploadProgress (progress, flag) {
        //这里可以通过回调的flag对不同上传域做处理
        this.progress = progress < 100 ? progress : 0;
        console.log(this.progress)
    },
    // 上传图片完成的回调
    uploadComplete(status, result, flag) {
        if (status == 200) { //
          let imgUrl = upImgBaseUrl + result.key //七牛域名 + 返回的key 组成文件url
          this.askParams.img.push(imgUrl)
        } else {
            //失败处理
        }
    },
    delUpImg(idx){
      let array = []
      this.askParams.img.forEach((item, i) =>{
        if (idx != i) {
          array.push(item)
        }
      })
      this.askParams.img = array
    },
    // 获取专家资料
    getExpert(){
      let urlStr = ''
      if (this.isLogin) {
        urlStr = '&uid='+this.userID
      }
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/zjdetail?id='+this.zjid+urlStr
      // console.log(getUrl)
      this.$http.get(getUrl).then(response => {
        let code = response.data.code
        if (code == 200) {
          this.expert = response.data.result
        }
        Indicator.close();
      }, response => {
        Toast('请求失败');
        console.log(response);
      });
    },
    isMobile(){
      var sUserAgent = navigator.userAgent;
    	var mobileAgents=['Android','iPhone','Symbian','WindowsPhone','iPod','BlackBerry','Windows CE'];
    	var goUrl = 0;
    	for( var i=0;i<mobileAgents.length;i++){
    			if(sUserAgent.indexOf(mobileAgents[i]) > -1){
    					goUrl = 1;
    					break;
    			}
    	}
      // console.log(goUrl)
    	if (goUrl != 1){
        console.log('pc')
    	}else {
    	  console.log('Mobile')
        this.$router.push('home')
    	}
    }
  },

}
</script>

